import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import AddButton from "../../../Ui/Button/AddButton";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import LmsModeratorCard from "./LmsModeratorCard";
import AddLmsModerator from "./AddLmsModerator";
import style from "../../FinanceManager/FinanceManager.module.css";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useLmsModerator } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";

const LmsModerator = ({ lmid, instituteId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [addModerator, setAddModerator] = useState(false);

  const { lmsModerator, lmsModeratorLoading, lmsModeratorRefetch } =
    useLmsModerator({
      data: {
        lmid: lmid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !lmid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (lmsModerator?.list?.length) {
      let obj = nextPage(lmsModerator?.list?.length);
      setState(() => obj);
    }
  }, [lmsModerator?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAddModerator((pre) => !pre);
  };

  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    lmsModeratorRefetch();
  }, [lmsModeratorRefetch]);

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("lms_moderators")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <AddButton
                label="add_new_moderator"
                onAction={onToggle}
                customStyle={{
                  padding: "0.6rem 1rem",
                }}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          {lmsModerator?.list?.map((moderator, index) =>
            lmsModerator?.list?.length === index + 1 ? (
              <div key={moderator?._id} ref={ref}>
                <LmsModeratorCard
                  moderator={moderator}
                  lmid={lmid}
                  instituteId={instituteId}
                  onRefetch={onRefetch}
                />
              </div>
            ) : (
              <LmsModeratorCard
                moderator={moderator}
                key={moderator?._id}
                lmid={lmid}
                instituteId={instituteId}
                onRefetch={onRefetch}
              />
            )
          )}
          {lmsModeratorLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            lmsModerator?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No moderator fuond related this search."
              />
            )
          ) : (
            lmsModerator?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No moderator added."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {addModerator && (
        <AddLmsModerator
          instituteId={instituteId}
          lmid={lmid}
          onClose={onToggle}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default LmsModerator;
