import { useState } from "react";
import style from "../../FinanceManager/Moderator/FinanceModerator.module.css";
import { useTranslation } from "react-i18next";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import { assestsFeedUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import InstituteStaff from "../../../UserStaffMember/InstituteStaff/InstituteStaff";
import { useLmsAddModerator } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import LmsReviewModeratorList from "./LmsReviewModeratorList";

const AddLmsModerator = ({ instituteId, lmid, onClose, onRefetch }) => {
  const { t } = useTranslation();
  const [activePopup, setActivePopup] = useState("OPEN_STAFF_LIST");
  const [selectedStaff, setSelectedStaff] = useState("");
  const [moderatorState, setModeratorState] = useState({
    mod_role: "",
    sid: "",
  });
  const [staffList, setStaffList] = useState([]);
  const [staffListId, setStaffListId] = useState([]);
  const [errorField, setErrorField] = useState("");
  const [lmsAddModerator] = useLmsAddModerator();

  const onSelectStaff = (staff) => {
    setSelectedStaff(staff);
    setModeratorState((prev) => ({
      ...prev,
      sid: staff?._id,
    }));
    setActivePopup("ASSIGN_ROLE");
  };

  const onSelectRole = (val) => {
    setModeratorState((prev) => ({
      ...prev,
      mod_role:
        val === "Leave Recommendation Authority"
          ? "LEAVE_RECOMMENDATION_ACCESS"
          : val === "Leave Review Authority"
          ? "LEAVE_REVIEW_ACCESS"
          : val === "Leave Sanction Authority"
          ? "LEAVE_SANCTION_ACCESS"
          : "",
    }));
  };

  const onSelectMultiStaff = (staff) => {
    if (staffListId?.includes(staff?._id)) {
      setStaffListId((prev) => prev?.filter((val) => val !== staff?._id));
      setStaffList((prev) => prev?.filter((val) => val?._id !== staff?._id));
    } else {
      setStaffListId((prev) => [...prev, staff?._id]);
      setStaffList((prev) => [...prev, staff]);
    }
  };

  const onAssignModerator = () => {
    let validation = requiredField(
      {
        role: moderatorState.mod_role,
        staff: staffListId?.[0],
      },
      []
    );

    if (existingRequiredField(validation) && instituteId && lmid) {
      setActivePopup("LOADING");
      lmsAddModerator({
        id: instituteId,
        addModerator: {
          mod_role: moderatorState.mod_role,
          sid: moderatorState.sid,
          staff_array:
            moderatorState.mod_role !== "LEAVE_SANCTION_ACCESS"
              ? staffListId
              : [],
          rev_array:
            moderatorState.mod_role === "LEAVE_SANCTION_ACCESS"
              ? staffListId
              : [],
          academic_department: null,
          lmid: lmid,
        },
      })
        .then(() => {
          onRefetch();
          setActivePopup("");
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  const onDone = () => {
    if (staffListId?.length > 0) setActivePopup("ASSIGN_ROLE");
  };
  return (
    <>
      {activePopup === "OPEN_STAFF_LIST" && (
        <InstituteStaff
          onSelectStaff={onSelectStaff}
          onClose={onClose}
          instituteId={instituteId}
        />
      )}
      {activePopup === "SELECT_MULTI" && (
        <InstituteStaff
          onSelectStaff={onSelectStaff}
          onClose={onClose}
          instituteId={instituteId}
          isMultiSelect
          onMultiSelect={onSelectMultiStaff}
          selectList={staffListId}
          onDone={onDone}
        />
      )}
      {activePopup === "SELECT_REVIEW_MULTI" && (
        <LmsReviewModeratorList
          onClose={() => setActivePopup("ASSIGN_ROLE")}
          onSelectReview={onSelectMultiStaff}
          lmid={lmid}
          selectedId={staffListId}
          onDone={onDone}
        />
      )}
      {activePopup === "ASSIGN_ROLE" && (
        <>
          <Modal onClose={onClose}>
            <div className={style.modal_container}>
              <UserMemberContentWrapper>
                <div className={style.modal_container_header}>
                  <h6>{t("assign_role_to_moderator")}</h6>
                </div>
              </UserMemberContentWrapper>
              <BorderBottom />
              <UserMemberContentWrapper
                customStyle={{
                  paddingTop: "0",
                }}
              >
                <section className={style.selected_staff_container}>
                  <img
                    src={
                      selectedStaff?.staffProfilePhoto
                        ? `${imageShowUrl}/${selectedStaff?.staffProfilePhoto}`
                        : `${assestsFeedUrl}/feed-user-avatar.svg`
                    }
                    alt="staff profile avatar"
                  />
                  <div className={style.selected_staff_text}>
                    <h6>
                      {`${selectedStaff?.staffFirstName ?? ""} ${
                        selectedStaff?.staffMiddleName ?? ""
                      } ${selectedStaff?.staffLastName ?? ""}`}
                    </h6>
                    <p>
                      {t("index_number")} {": "}
                      {selectedStaff?.staffROLLNO ?? ""}
                    </p>
                  </div>
                </section>

                <CustomSelect
                  selectLabel={t("select_role_of_moderator")}
                  selectedValue={t("please_select_role")}
                  options={[
                    "Leave Recommendation Authority",
                    "Leave Review Authority",
                    "Leave Sanction Authority",
                  ]}
                  defalutValue={
                    moderatorState?.mod_role === "LEAVE_RECOMMENDATION_ACCESS"
                      ? "Leave Recommendation Authority"
                      : moderatorState?.mod_role === "LEAVE_REVIEW_ACCESS"
                      ? "Leave Review Authority"
                      : moderatorState?.mod_role === "LEAVE_SANCTION_ACCESS"
                      ? "Leave Sanction Authority"
                      : ""
                  }
                  onClick={onSelectRole}
                  errorShow={errorField["role"]}
                />

                {staffList?.length > 0 ? (
                  staffList?.map((staff) => (
                    <section
                      className={style.selected_staff_container}
                      style={{
                        cursor: "pointer",
                      }}
                      onClick={() => setActivePopup("SELECT_MULTI")}
                      key={staff?._id}
                    >
                      <img
                        src={
                          staff?.staffProfilePhoto ??
                          staff?.access_staff?.staffProfilePhoto
                            ? `${imageShowUrl}/${
                                staff?.staffProfilePhoto ??
                                staff?.access_staff?.staffProfilePhoto
                              }`
                            : `${assestsFeedUrl}/feed-user-avatar.svg`
                        }
                        alt="staff profile avatar"
                      />
                      <div className={style.selected_staff_text}>
                        {staff?.staffFirstName ||
                        staff?.access_staff?.staffFirstName ? (
                          <h6>
                            {`${
                              staff?.staffFirstName ??
                              staff?.access_staff?.staffFirstName
                            } ${
                              staff?.staffMiddleName ??
                              staff?.access_staff?.staffMiddleName ??
                              ""
                            } ${
                              staff?.staffLastName ??
                              staff?.access_staff?.staffLastName
                            }`}
                          </h6>
                        ) : null}
                        <p>
                          {t("index_number")} {": "}
                          {staff?.staffROLLNO ??
                            staff?.access_staff?.staffROLLNO ??
                            ""}
                        </p>
                      </div>
                    </section>
                  ))
                ) : (
                  <section
                    className={style.selected_staff_container}
                    style={{
                      cursor: "pointer",
                    }}
                    onClick={() =>
                      setActivePopup(
                        moderatorState?.mod_role === "LEAVE_SANCTION_ACCESS"
                          ? "SELECT_REVIEW_MULTI"
                          : "SELECT_MULTI"
                      )
                    }
                  >
                    <img
                      src={`${assestsFeedUrl}/feed-user-avatar.svg`}
                      alt="staff profile avatar"
                    />
                    <div className={style.selected_staff_text}>
                      <h6>
                        {t(
                          moderatorState?.mod_role ===
                            "LEAVE_RECOMMENDATION_ACCESS"
                            ? "select_recommendation_staff"
                            : moderatorState?.mod_role ===
                              "LEAVE_SANCTION_ACCESS"
                            ? "select_review_authority"
                            : "select_review_staff"
                        )}{" "}
                        {errorField["staff"] ? (
                          <span
                            style={{
                              color: "red",
                            }}
                          >
                            * required
                          </span>
                        ) : (
                          ""
                        )}
                      </h6>
                      <p>{t("index_number")}</p>
                    </div>
                  </section>
                )}

                <CreateButton label="assign" onAction={onAssignModerator} />
              </UserMemberContentWrapper>
            </div>
          </Modal>
        </>
      )}
      {activePopup === "LOADING" && <QvipleLoader />}
    </>
  );
};

export default AddLmsModerator;
