import React from "react";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import style from "../../FinanceManager/Moderator/FinanceModerator.module.css";
import { useTranslation } from "react-i18next";
import { assestsUserAdmissionUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";

const LmsReviewModeratorListCard = ({ review, selectedId, onSelect }) => {
  const { t } = useTranslation();
  return (
    <section
      className={style.selected_staff_container}
      style={{
        cursor: "pointer",
      }}
      onClick={() => onSelect(review)}
      key={review?._id}
    >
      {selectedId?.includes(review?._id) ? (
        <img
          src={`${assestsUserAdmissionUrl}/check.svg`}
          alt="icon"
          style={{
            height: "1.3rem",
            borderRadius: "0",
            cursor: "pointer",
          }}
        />
      ) : (
        <img
          style={{
            height: "1.3rem",
            borderRadius: "0",
            cursor: "pointer",
          }}
          src={`${assestsUserAdmissionUrl}/uncheck.svg`}
          alt="icon"
        />
      )}
      <img
        src={
          review?.access_staff?.staffProfilePhoto
            ? `${imageShowUrl}/${review?.access_staff?.staffProfilePhoto}`
            : "/images/member_tab/class/default_avatar.svg"
        }
        alt="staff profile avatar"
      />
      <div className={style.selected_staff_text}>
        <h6>
          {`${review?.access_staff?.staffFirstName} ${
            review?.access_staff?.staffMiddleName ?? ""
          } ${review?.access_staff?.staffLastName}`}
        </h6>
        <p>{t("leave_review_authority")}</p>
      </div>
    </section>
  );
};

export default LmsReviewModeratorListCard;
