import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import { useState } from "react";

import { useLmsDeleteModerator } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";

const LmsModeratoMenu = ({
  onClose,
  lmid,
  openModeratorMenu,
  instituteId,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [lmsDeleteModerator] = useLmsDeleteModerator();

  const onDelete = () => {
    if (lmid && instituteId && openModeratorMenu?._id) {
      setDisabled((pre) => !pre);
      lmsDeleteModerator({
        lmid: lmid,
        id: instituteId,
        mid: openModeratorMenu?._id,
        flow: "LMS",
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onDelete}>{t("delete")}</h6>
      </ModalMenu>
    </>
  );
};

export default LmsModeratoMenu;
