import style from "../../PlateformUser/PlateformUser.module.css";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import LmsReviewModeratorListCard from "./LmsReviewModeratorListCard";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useLmsReviewModeratorList } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import CreateButton from "../../../Ui/Button/CreateButton";
const LmsReviewModeratorList = ({
  onClose,
  onSelectReview,
  lmid,
  selectedId,
  onDone,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { lmsReviewModeratorList, lmsReviewModeratorListLoading } =
    useLmsReviewModeratorList({
      data: {
        lmid: lmid,
        role: "LEAVE_REVIEW_ACCESS",
        page: page,
        limit: 10,
        search: search,
      },
      skip: !lmid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (lmsReviewModeratorList?.list?.length) {
      let obj = nextPage(lmsReviewModeratorList?.list?.length);
      setState(() => obj);
    }
  }, [lmsReviewModeratorList?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onSelect = (value) => {
    onSelectReview(value);
  };

  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("select_review_authority")}</h6>
            </div>
            <div className={style.modal_container_btn_container}>
              <CreateButton
                label={t("select")}
                count={selectedId}
                onAction={onDone}
                customStyle={{
                  margin: "0",
                  padding: "0.3rem 0.5rem",
                }}
              />

              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "1rem",
          }}
        >
          <div
            className={style.assign_search_container}
            style={{
              marginBottom: "1rem",
            }}
          >
            <input
              type="text"
              placeholder={t("search")}
              className={style.assign_search_input}
              onChange={onSearchEvent}
              style={{
                width: "100%",
              }}
            />
            <img
              src={`${assestsNavbarUrl}/navbar-search.svg`}
              alt="search icon"
            />
          </div>

          {lmsReviewModeratorList?.list?.map((review, index) =>
            lmsReviewModeratorList?.list?.length === index + 1 ? (
              <div key={review?._id} ref={ref}>
                <LmsReviewModeratorListCard
                  review={review}
                  selectedId={selectedId}
                  onSelect={onSelect}
                />
              </div>
            ) : (
              <LmsReviewModeratorListCard
                review={review}
                key={review?._id}
                selectedId={selectedId}
                onSelect={onSelect}
              />
            )
          )}
          {lmsReviewModeratorListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            lmsReviewModeratorList?.list?.length === 0 && (
              <EmptyMessage
                title="No review moderator found related this search."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          ) : (
            lmsReviewModeratorList?.list?.length === 0 && (
              <EmptyMessage
                title="No review moderator."
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default LmsReviewModeratorList;
