import { useTranslation } from "react-i18next";
import style from "../../FinanceManager/Moderator/FinanceModerator.module.css";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import LmsModeratoMenu from "./LmsModeratoMenu";
import { useState } from "react";

const LmsModeratorCard = ({ moderator, instituteId, lmid, onRefetch }) => {
  const { t } = useTranslation();
  const [moderatorMenu, setModeratorMenu] = useState("");

  const onMenu = () => {
    setModeratorMenu(moderator);
  };
  const onClose = () => {
    setModeratorMenu("");
  };
  return (
    <>
      <div className={style.moderator_card}>
        <img
          src={
            moderator?.access_staff?.staffProfilePhoto
              ? `${imageShowUrl}/${moderator?.access_staff?.staffProfilePhoto}`
              : `${assestsFeedUrl}/feed-user-avatar.svg`
          }
          alt="moderator avatar"
        />
        <section className={style.moderator_card_text_container}>
          <h6>
            {`${moderator?.access_staff?.staffFirstName} ${
              moderator?.access_staff?.staffMiddleName ?? ""
            } ${moderator?.access_staff?.staffLastName}`}
          </h6>
          <p>
            {t("index_number")} {" : "}
            {moderator?.access_staff?.staffROLLNO}
          </p>
          <p>
            {moderator?.access_role === "LEAVE_RECOMMENDATION_ACCESS"
              ? "Leave Recommendation Authority"
              : moderator?.access_role === "LEAVE_REVIEW_ACCESS"
              ? "Leave Review Authority"
              : moderator?.access_role === "LEAVE_SANCTION_ACCESS"
              ? "Leave Sanction Authority"
              : ""}
          </p>
        </section>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onMenu}
          className={style.menu_icon}
          title="Menu"
        />
      </div>
      {moderatorMenu && (
        <LmsModeratoMenu
          onClose={onClose}
          lmid={lmid}
          openModeratorMenu={moderatorMenu}
          instituteId={instituteId}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default LmsModeratorCard;
